











































/* global rules*/
body {
  background-color:#000;
  font-family: 'Play', sans-serif;
  line-height:2;
}
a, a:hover {
  color:inherit
}
.mainFont {
  font-family: 'Press Start 2P', cursive;
}
.rounded-circle {
  border-radius:50%;
}
.group-section {
  background:url(/static/img/floor08.5a4c05b.png) no-repeat bottom;
  margin-bottom:120px;
}
.group-section .layout {
  padding:30px;
  margin-top:60px;
  background:-webkit-gradient(linear,left top, left bottom,from(rgba(0, 51, 204,.2)),to(rgba(0, 51, 204,.2))), url(/static/img/pic_01.5d09359.png) fixed;
  background:linear-gradient(rgba(0, 51, 204,.2),rgba(0, 51, 204,.2)), url(/static/img/pic_01.5d09359.png) fixed;
}
.group-section .layout .btn{
  background-color:#c00;
}
/*global responsive*/
@media screen and (min-width:993px) {
.container {
    width:85%;
}
}

nav {
  background:-webkit-gradient(linear,left top, left bottom,from(rgba(8, 8, 91, 0.4)),to(rgba(8, 8, 91, 0.4))), url(data:image/png;base64,)  center;
  background:linear-gradient(rgba(8, 8, 91, 0.4),rgba(8, 8, 91, 0.4)), url(data:image/png;base64,)  center;
  background:#c00;
}
nav .brand-logo {
    font-size:24px;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;
}
nav .brand-logo img {
  max-width: 55px;
  max-height: 55px;
  margin:0 10px;
}
nav form {
  position: relative;
  height: 100%;
  background: inherit;
}
.nav-wrapper{
  background: inherit;
}
.search-close-icon{
  display: none!important;
  position: absolute;
  top: 0;
  left: 100%!important;
  padding-left: 0px;
  color: rgba(0, 0, 0, 0.54);
  -webkit-transition: color .2s;
  transition: color .2s;
  cursor: pointer;
}
.my-grey{
  background: #f7f7f7;
}
input.search-field {
  font-weight: 500;
  color: rgba(0, 0, 0, 0.54);
  border-radius: 2px;
  border: 0;
  border-radius: 5px;
  height: 36px;
  padding: 8px 8px 8px 60px;
  width: 200px;
  background: #f7f7f7;
  -webkit-transition: background 0.2s, width .2s;
  transition: background 0.2s, width .2s;
}
input.search-field:hover {
  background: #ebebeb;
}
.searchbox {
  position: relative;
}
::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.54);
}
::-moz-placeholder {
  color: rgba(0, 0, 0, 0.54);
}
::-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.54);
}
::placeholder {
  color: rgba(0, 0, 0, 0.54);
}
input.search-field:focus {
  outline: none;
  width: 500px;
  color: #212121;
}
input.search-field:focus:hover {
  background: #f7f7f7;
}
.search-icon {
  position: absolute;
  top: 0;
  padding-left: 20px;
  color: rgba(0, 0, 0, 0.54);
}
.search-content {
  color: #303c42;
  width: 90%;
  margin: auto;
  height: auto;
  padding: 8px 10px;
}
.search-heading {
  color: #757575;
  display: block;
  font-size: 12px;
  line-height: 30px;
}
input.search-field ~ .search-popup {
  overflow: hidden;
  left: 0px;
  min-height: 50px;
  max-height: 700px;
  width: 200px;
  top: -450px; 
  position: absolute;
  z-index: 1;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: width .2s, top .2s ease-in-out;
  transition: width .2s, top .2s ease-in-out;
  border-radius: 2px;
  background-color: #fff;
}
input.search-field:focus ~ .search-popup {
  width: 500px;
  top: 50px;
}
.top-margin {
  margin-top: 8px;
}
.dropdown-content {
  top: 70px  !important;
  border-radius:5px  !important;
  min-width:120px !important;
}
.dropdown-content li > a, .dropdown-content li > span {
    color:#173893;
}
.sidenav {
  font-size:30px;
}
.sidenav .brand-logo {    
    color: #c00;
    position: relative;
    font-size: 14px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 20px;
}
.sidenav li > a {
    color:#173893;
    display: block;
    font-size: 18px;
    font-weight: 500;
    line-height: 48px;
    padding:30px;
}
.sidenav-overlay {
    background-color: rgba(0,0,0,0);
}
@media only screen and (max-width: 992px) {
nav .brand-logo {
    font-size:15px;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    right: 0px;
    left: auto;
}
nav .brand-logo img {
    max-width:40px;
    max-height:40px;
}
}
@media only screen and (max-width: 600px) {
::-webkit-input-placeholder {
    color: transparent;
}
::-moz-placeholder {
    color: transparent;
}
::-ms-input-placeholder {
    color: transparent;
}
::placeholder {
    color: transparent;
}
input.search-field {
    width: 1px;
    margin-right: 0px;
    padding: 8px 25px;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
}
input.search-field ~ .search-popup {
    width: 1px;
    margin-right: 0px;
    padding: 8px 25px;
}
.search-icon {
    top: 0;
    padding-left: 15px;
    color: rgba(0, 0, 0, 0.54);
}
input.search-field:focus {
    margin-right: 0px;
    width: 100vw;
    padding: 8px 8px 8px 50px;
}
input.search-field:focus ~ .search-popup {
    width: 100vw;
}
.search-content {
    width: 100%;
    padding: 8px 8px;
}
nav .nav-wrapper {
    margin-right: 0px;
}
}

.footerLayout {
    padding:60px 0;
    height:400px;
    position:relative;
    text-align: center;
    color:#c00;
}
.footerLayout:before {
    content:'';
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background:url(/static/img/479216be7eb7443c6f03bd7711bfb348bbd34b1c1506db-qPs4Ni_fw1200.14c1a97.png) center;
    background-size:contain;
    opacity:.2
}
.footerLayout h3 {
    background-size:contain;
    text-align:center;
    font-size:16px;
    margin-top:10px;
}
.footerLayout img {
    max-width: 50px;
}
.footerLayout p {
    letter-spacing: 3px;
    margin-top:30px;
}
.footer {
    margin:0 10px;
}

.home h3 {
  font-size:22px;
  position:relative;
  padding-left:45px;
}
.home h3:before {
  content:'';
  position: absolute;
  width:45px;
  height:45px;
  top: -9px;
  left: 0px;
  background:url(data:image/png;base64,) no-repeat;
}
.home .group-section:nth-of-type(1) h3:before {
  background-position:0 0;
}
.home .group-section:nth-of-type(2) h3:before {
  background-position:-45px 0px;
}
.home .group-section:nth-of-type(3) h3:before {
  background-position:-90px 0;
}
@media only screen and (max-width: 767px) {
.home h3 {
    font-size:16px;
}
.home h3 img {
    width:40px;
}
}

